import React, { useMemo, useRef, useState } from "react";
import styles from "../css/PostWrite.module.css";
import { useAuthContext } from "../context/AuthContext";
import { postPost } from "../service/api";
import { Link, useNavigate } from "react-router-dom";
import "react-quill/dist/quill.snow.css";
import axios from "axios";
import ReactQuill from "react-quill";

const PostWrite = () => {
  const navigate = useNavigate();

  const [mbti, setMbti] = useState(null);

  const [selectEMbti, setSelectEMbti] = useState("");
  const [selectIMbti, setSelectIMbti] = useState("");

  const [value, setValue] = useState("");
  const quillRef = useRef();


  const imageHandler = () => {
    console.log('      !');
  
    // 1.   input type=file DOM .
    const input = document.createElement('input');
    //  
    input.setAttribute('type', 'file');
    input.setAttribute('accept', 'image/*');
    input.click(); //     input .

    input.addEventListener('change', async () => {
      console.log('');
      const file = input.files[0];
      // multer    .
      const formData = new FormData();
      formData.append('img', file); // formData - 
      //  multer  .
      try {
        const result = await axios.post('https://192.168.5.17:10000/api/post/img', formData);
        console.log(' ,   ', result.data.url);
        const IMG_URL = result.data.url;

        const editor = quillRef.current.getEditor(); //   

        const range = editor.getSelection();
        //    
        editor.insertEmbed(range.index, 'image', IMG_URL);
      } catch (error) {
        console.log('');
      }
    });
  };

  const modules = useMemo(() => {
    return {
      toolbar: {
        container: [
          ['image'],
          [{ header: [1, 2, 3, false] }],
          ['bold', 'italic', 'underline', 'strike', 'blockquote'],
        ],
        handlers: {
          //     imageHandler   .
          image: imageHandler,
        },
      },
    };
  }, []);
  const formats = [
    'header',
    'bold',
    'italic',
    'underline',
    'strike',
    'blockquote',
    'image',
  ];

  

  const handleEMbtiChange = (e) => {
    setSelectEMbti(e.target.value);
    setSelectIMbti("I");
  };
  const handleIMbtiChange = (e) => {
    setSelectIMbti(e.target.value);
    setSelectEMbti("E");
  };
  const { memoUserInfo } = useAuthContext();
  const { isLoggedIn, userInfo } = memoUserInfo;

  const handleMbtiClick = (e) => {
    if (e.target.value == "E") {
      setMbti(null);
    } else if (e.target.value == "I") {
      setMbti(null);
    } else {
      setMbti(e.target.value);
    }
  };
  const handleSubmit = async (e) => {
    e.preventDefault();
    const body = {
      title: e.target.title.value,
      content: value,
      category: mbti,
    };
    const result = await postPost(body);

    if (result.message == "success") {
      alert("");
      navigate("/post/list");
    }
  };
  if (!isLoggedIn) {
    return (
      <>
        <h2>    </h2>
        <Link to="/"> </Link>
        <br />
        <Link to="/post/list"> </Link>
      </>
    );
  }

  return (
    <>
      <div className={styles.container}>
        {mbti == null ? (
          <h3>  </h3>
        ) : (
          <h3>{mbti}  </h3>
        )}
        <form className={styles.form} onSubmit={handleSubmit}>
          <div className={styles.selectBox}>
            <div className={styles.selectTitle}> </div>
            <div>
            <select
              onChange={handleEMbtiChange}
              value={selectEMbti}
              onClick={handleMbtiClick}
              className={styles.mbtiSelectE}
              id="E"
            >
              <option>E</option>
              <option value="ESTJ">ESTJ</option>
              <option value="ESTP">ESTP</option>
              <option value="ESFJ">ESFJ</option>
              <option value="ESFP">ESFP</option>
              <option value="ENTJ">ENTJ</option>
              <option value="ENTP">ENTP</option>
              <option value="ENFJ">ENFJ</option>
              <option value="ENFP">ENFP</option>
            </select>
            <select
              onChange={handleIMbtiChange}
              value={selectIMbti}
              onClick={handleMbtiClick}
              className={styles.mbtiSelectI}
              id="I"
            >
              <option>I</option>
              <option value="ISTJ">ISTJ</option>
              <option value="ISTP">ISTP</option>
              <option value="ISFJ">ISFJ</option>
              <option value="ISFP">ISFP</option>
              <option value="INTJ">INTJ</option>
              <option value="INTP">INTP</option>
              <option value="INFJ">INFJ</option>
              <option value="INFP">INFP</option>
            </select>
          </div>
          </div>
          <div className={styles.titleBox}>
            <input
              className={styles.titleInput}
              type="text"
              name="title"
              placeholder=" "
              required
            />
          </div>
          <div className={styles.contentBox}>
          <ReactQuill
            ref={quillRef}
            theme="snow"
            placeholder=""
            value={value}
            onChange={setValue}
            modules={modules}
            formats={formats}
            className={styles.editorBox}
          />
          </div>

          <button type="submit" className={styles.submitBtn}>
            
          </button>
        </form>
      </div>
    </>
  );
};

export default PostWrite;
